</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .footer {
    text-align: center;
    height: 150px;
    background-color: $footer-color;
    border-top: 2px solid $body-background-color;
    #logo {
      color: $white-color;
      font-size: rem(22);
      font-weight: 400;
    }
