import Vue from 'vue';
import Router from 'vue-router';
import Homepage from '@/pages/Homepage';
import Portfolio from '@/pages/Portfolio';
import PortfolioCase from '@/pages/PortfolioCase';
import Services from '@/pages/Services';
import ServicesPortfolio from '@/pages/ServicesPortfolio';
import About from '@/pages/About';
import Feedbacks from '@/pages/Feedbacks';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faCoffee, faEnvelope, faMapMarkerAlt, faMobile } from '@fortawesome/free-solid-svg-icons';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

library.add(faCoffee, faEnvelope, faMapMarkerAlt, faMobile);
library.add(fab);

Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.use(Router);
Vue.component('Homepage', Homepage);
Vue.component('Portfolio', Portfolio);
Vue.component('Services', Services);
Vue.component('ServicesPortfolio', ServicesPortfolio);
Vue.component('About', About);
Vue.component('Feedbacks', Feedbacks);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage,
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio,
    },
    {
      path: '/portfolio/case/:case_id',
      name: 'PortfolioCase',
      component: PortfolioCase,
    },
    {
      path: '/services',
      name: 'Services',
      component: Services,
    },
    {
      path: '/services/cases',
      name: 'ServicesPortfolio',
      component: ServicesPortfolio,
    },
    {
      path: '/about',
      name: 'About',
      component: About,
    },
    {
      path: '/about/feedbacks',
      name: 'Feedbacks',
      component: Feedbacks,
    },
  ],
});



