<template>
  <div class="about">
    <SloganLine :slogan="slogan"/>
    <div class="about--comment">
      <v-layout row>
        <v-flex>
          <slider :indicators="false" height="200px" :autoplay="false" animation="fade">
            <slider-item
              v-for="(comment, index) in comments"
              :key="index"
              :autoplay="false"
            >
              <div class="comment-block">
                <div class="comment-block__logo">
                  <img class="avatar" :src="comment.avatarUrl" alt="">
                  <span class="client">{{comment.client_name}}</span>
                </div>
                <div class="comment-block__text">
                  <h3 class="comment">{{comment.text}}</h3>
                </div>
              </div>
            </slider-item>
          </slider>
        </v-flex>
      </v-layout>
    </div>
    <div class="about--photo">
      <v-layout>
        <v-flex md3 xl3 v-for="(photo, i) in photos" :key="i">
          <div class="about--photo__item">
            <img :src="photo.url" alt="">
          </div>
        </v-flex>
      </v-layout>
    </div>
    <div class="about--main-content">
      <v-layout align-center justify-center row fill-height>
        <v-flex xl6 class="text-block__wrapper">
          <div class="text-block">
            <p>My name is Nick Golensky. For over a decade, I have been self-employed as a
              professional freelance translator, reviewer, LQA specialist and linguist team
              leader, working in such language pairs as English-Russian and English-
              Ukrainian.
              </p><p>
              During my linguistic practice, I completed projects for such major London
              agencies as Hogarth Worldwide/Native, Wordbank, World Writers/Tag
              Worldwide, Global Communicators, and Lingo24.
          </p><p>
              Working as a head localization specialist for Dropbox and Mail.Ru on regular
              basis, among my main end clients there are also such brands as Sony, HTC,
              Symantec, and Microsoft. Having been qualified as an expert of ProZ.com
              Certified Pro Network (according to EN 15038),
          </p><p>
              I also became a linguist of the charitable organisation Translators without
              Borders. On ProZ.com, I reached Top 1 in Willingness to Work Again rating
              (according to clients’ feedbacks) among ca. 25,000 English to Russian
              translators, and was granted a Top Rated status on Upwork.com.</p>
            <v-layout
              row wrap
              align-center
              justify-center>
              <v-flex
                md12 xl12
                class="about-button">
                <router-link
                  class="about-button--item"
                  :to="{name: 'Feedbacks'}">See more feedbacks</router-link>
              </v-flex>
            </v-layout>
          </div>
        </v-flex>
        <v-flex xl6 class="contact-block__wrapper">
          <div class="contact-block">
            <div class="contact-block__item">
              <font-awesome-icon icon="envelope"/>
              <span>nick@golensky.com</span>
            </div>
            <div class="contact-block__item">
              <font-awesome-icon
                :icon="['fab', 'skype']" />
              <span>golensky</span>
            </div>
            <div class="contact-block__item">
              <font-awesome-icon
                :icon="['fab', 'telegram']" />
              <span>@golensky</span>
            </div>
            <div class="contact-block__item">
              <font-awesome-icon icon="mobile"/>
              <span>+380 50 96 26 539</span>
            </div>
            <div class="contact-block__item">
              <font-awesome-icon icon="map-marker-alt"/>
              <span>90-94 Honcha Str. Chernihiv 14005 Ukraine</span>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import { Slider, SliderItem } from 'vue-easy-slider';
import SloganLine from '../components/shared/SloganLine';

export default {
  name: 'About',
  components: {
    SloganLine,
    Slider,
    SliderItem,
  },
  data() {
    return {
      slogan: 'IDEALISTIC ABOUT QUALITY',
      photos: [{
        url: '/static/1.png',
      }, {
        url: '/static/2.png',
      }, {
        url: '/static/3.png',
      }, {
        url: '/static/4.png',
      }],
      comments: [{
        client_name: 'ARCHLINUX',
        text: 'Good response, and he proceeded to clarify the contents of the translation before starting work, for the quality. He is a trustworthy translator.',
        avatarUrl: '/static/client_logo.png',
      }, {
        client_name: 'ARCHLINUX',
        text: 'Good response, and he proceeded to clarify the contents of the translation before starting work, for the quality. He is a trustworthy translator.',
        avatarUrl: '/static/client_logo.png',
      }],
      list: [
        { backgroundColor: '#3f51b5', width: '100%', height: '100%' },
        { backgroundColor: '#eee', width: '100%', height: '100%' },
        { backgroundColor: '#f44336', width: '100%', height: '100%' },
      ],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .slider {
    margin: 0 rem(278);
    height: 100%;
    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    button.slider-btn.slider-btn-left {
      background: $white-color;
      i {
        color: $navigation-link-active;
        transform: rotate(0deg);
        &:after {
          content: '';
          height: 32px;
          width: 16px;
          background: url('../assets/about/arrow_left.png');
          position: absolute;
          left: -5px;
        }
      }
    }
    button.slider-btn.slider-btn-right {
      background: $white-color;
      i {
        color: $navigation-link-active;
        transform: rotate(180deg);
        &:after {
          content: '';
          height: 32px;
          width: 16px;
          background: url('../assets/about/arrow_left.png');
          position: absolute;
          right: -5px;
          top: 0;
        }
      }
    }
  }
  .about {
    .comment-block {
      &__logo {
        display: inline-block;
        text-align: center;
        max-width: rem(126);
        width: rem(126);
        margin-right: rem(170);
        img {
          width: 100%;
        }
        span {
          display: block;
          font-size: rem(17);
          font-weight: 600;
          color: $navigation-link-active;
          margin-top: $basic-padding;
        }
      }
      &__text {
        display: inline-block;
        .comment {
          position: relative;
          text-align: left;
          max-width: 646px;
          width: 100%;
          color: $navigation-link-active;
          &:before {
            content: '';
            transform: rotate(0deg);
            height: 30px;
            width: 30px;
            background: url('../assets/about/quotes.png');
            position: absolute;
            left: -45px;
            top: -30px;
          }
          &:after {
            content: '';
            transform: rotate(180deg);
            height: 30px;
            width: 30px;
            background: url('../assets/about/quotes.png');
            position: absolute;
            right: -25px;
            bottom: -30px;
          }
        }
      }
    }
    &--main-content {
      .text-block {
        display: inline-block;
        max-width: rem(530);
        width: 100%;
        text-align: left;
        margin-right: rem(80);
        &__wrapper {
          text-align: right;
          background-color: $white-color;
          height: 100%;
        }
      }
      .contact-block {
        display: inline-block;
        width: 100%;
        max-width: rem(420);
        text-align: left;
        color: $white-color;
        font-size: rem(32);
        margin: auto 0 auto rem(80);
        &__item {
          padding-bottom: rem(32);
        }
        &__wrapper {
          display: flex;
          align-content: center;
          justify-content: flex-start;
          text-align: left;
          height: rem(630);
          padding: $slogan-margin 0;
          @include slogan-gradient;
        }
      }
    }
    &-button {
      text-align: center;
      margin-top: rem(25);
      &--item {
        text-align: center;
        display: inline-block;
        border: 1px solid $button-color;
        max-width: 428px;
        width: 100%;
        padding: $basic-padding 0;
        color: $button-color;
        &:hover {
          color: $white-color;
          background-color: $button-color;
        }
        &_active {
          color: $white-color;
          background-color: $button-color;
        }
      }
    }
    &--photo {
      &__item {
        width: 100%;
        height: 100%;
        border: 1px solid $white-color;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

</style>



