<template>
  <div class="services-portfolio">
    <v-layout
      row wrap
      align-center
      justify-center
      class="services-portfolio-button-wrapper">
      <v-flex
        md6 xl6
        v-for="(service,i) in services"
        :key="i"
        :class="[i % 2 === 0? 'left' : 'right']"
        class="services-portfolio-button">
        <a class="services-portfolio-button--item" href="">{{service}}</a>
      </v-flex>
    </v-layout>
    <v-layout
      row wrap
      align-center
      justify-center
      class="services-portfolio-case-wrapper">
      <SingleCaseCell
        v-for="(someCase,i) in cases"
        :key="i"
        :someCase="someCase"/>
    </v-layout>
    <v-layout
      row wrap
      align-center
      justify-center
      class="services-portfolio-pagination-wrapper">
      <v-flex md6 xl6>
        <v-pagination
          v-model="page"
          :length="4"
          class="services-portfolio-pagination"
        ></v-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import SingleCaseCell from '../components/shared/SingleCaseCell';

export default {
  name: 'ServicesCases',
  props: ['service'],
  components: {
    SingleCaseCell,
  },
  data() {
    return {
      page: 1,
      services: ['Translation', 'Localization', 'Proofreading & Editing',
        'Quality assurance', 'Transcreation', 'Market research',
        'SEO Adaptation', 'Project management'],
      cases: [{
        case_id: 0,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 1,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 2,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 4,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .services-portfolio-button {
    &-wrapper {
      padding: $slogan-margin 0 $button-margin;
      @include slogan-gradient();
    }
    &.left {
      text-align: right;
      padding-right: $button-margin;
    }
    &.right {
      text-align: left;
      padding-left: $button-margin;
    }
    &--item {
      text-align: center;
      display: inline-block;
      border: 1px solid $button-color;
      max-width: 428px;
      width: 60%;
      padding: $basic-padding 0;
      margin-bottom: $button-margin;
      color: $button-color;
      background-color: $white-color;
      &:hover {
        color: $white-color;
        background-color: $button-color;
      }
      &_active {
        color: $white-color;
        background-color: $button-color;
      }
    }
  }
  .services-portfolio-pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    &-wrapper {
      padding: $side-margin 0;
      @include slogan-gradient();
    }
    li {
      display: inline-block;
      height: 60px;
      width: 60px;
      margin-right: 2px;
      &:first-child {
        margin-right: 100px;
      }
      &:last-child {
        margin-left: 100px;
      }
    }
    button {
      display: inline-block;
      width: 100%;
      height: 60px;
      margin: 0;
    }
    .v-pagination__navigation {
      color: $white-color !important;
      box-shadow: none;
      font-size: rem(32);
      background-color: transparent !important;
      i {
        color: $white-color !important;
      }
    }
    .v-pagination__item {
      font-size: rem(32);
      color: $white-color !important;
      font-weight: 300;
      background-color: transparent !important;
      box-shadow: none;
      border-radius: 0;
      min-width: 60px !important;
      padding: 0 !important;
      &:hover {
        border: 1px solid $white-color;
      }
      &.primary {
        width: 100%;
        padding: 0 !important;
        background-color: $navigation-link-active !important;
        border-color: $navigation-link-active !important;
      }
    }
  }
</style>



