<template>
  <div class="navbar">
    <div class="navbar-logo">
      <router-link
        :to="{name: 'Homepage'}"></router-link>
      <img src="../../assets/logo.svg" alt="">
    </div>
    <div class="navbar-nav">
      <router-link
        class="navbar-nav--link"
        :to="{name: 'Portfolio'}">PORTFOLIO</router-link>
      <router-link
        class="navbar-nav--link"
        :to="{name: 'Services'}">SERVICES</router-link>
      <router-link
        class="navbar-nav--link"
        :to="{name: 'About'}">ABOUT</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    padding: $basic-padding $side-margin;
    z-index: 2;
    @include navigation-shadow;
    .router-link-active {
      color: $navigation-link-active;
    }
    &-logo {
      display: flex;
      a {
        position: absolute;
        margin: auto 0;
        width: rem(400);
        height: rem(39);
      }
      img {
        width: 100%;
        max-width: rem(400);
        max-height: rem(39);
      }
    }
    &-nav {
      &--link {
        font-size: rem(15);
        font-weight: 600;
        @include letter-spacing(140);
        color: $navigation-link;
        margin-right: $side-margin;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
</style>



