<template>
  <div class="portfolio">
    <SloganLine :slogan="slogan"/>
    <v-layout
      row wrap
      align-center
      justify-center
      class="portfolio-button-wrapper">
      <v-flex
        md6 xl6
        v-for="(category,i) in categories"
        :key="i"
        :class="[i % 2 === 0? 'left' : 'right']"
        class="portfolio-button">
        <a class="portfolio-button--item" href="">{{category}}</a>
      </v-flex>
    </v-layout>
    <v-layout
      row wrap
      align-center
      justify-center
      class="portfolio-case-wrapper">
      <SingleCaseCell
        v-for="(someCase,i) in cases"
        :key="i"
        :someCase="someCase"/>
      <v-flex
        md6 xl6
        v-for="(someCase,i) in cases"
        :key="i">
        <div class="portfolio-case">
          <router-link
            class="link"
            :to="{name: 'PortfolioCase', params: {case_id: someCase.case_id}}">
          </router-link>
          <div class="description">
            <div class="customer-details">
              <p><b>{{someCase.customer}}</b><span>/</span>{{someCase.country}}</p>
            </div>
            <h2>{{someCase.header}}</h2>
            <h3 class="hover-info">{{someCase.description}}</h3>
          </div>
          <img :src="someCase.src" alt="">
        </div>
      </v-flex>
    </v-layout>
    <v-layout
      row wrap
      align-center
      justify-center
      class="portfolio-pagination-wrapper">
      <v-flex md6 xl6>
        <v-pagination
          v-model="page"
          :length="4"
          class="portfolio-pagination"
        ></v-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import SloganLine from '../components/shared/SloganLine';
import SingleCaseCell from '../components/shared/SingleCaseCell';

export default {
  name: 'Portfolio',
  components: {
    SloganLine,
    SingleCaseCell,
  },
  data() {
    return {
      slogan: 'LANGUAGE SPECIFIC.',
      page: 1,
      categories: ['Marketing', 'Business & Financial', 'It & Games',
        'Technical', 'Hi-Tec', 'Fictional', 'Legal', 'Articles'],
      cases: [{
        case_id: 0,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 1,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 2,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 4,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }],
    };
  },
  methods: {
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .portfolio-button {
    &-wrapper {
      margin-top: $slogan-margin;
      margin-bottom: $button-margin;
    }
    &.left {
      text-align: right;
      padding-right: $button-margin;
    }
    &.right {
      text-align: left;
      padding-left: $button-margin;
    }
    &--item {
      text-align: center;
      display: inline-block;
      border: 1px solid $button-color;
      max-width: 428px;
      width: 60%;
      padding: $basic-padding 0;
      margin-bottom: $button-margin;
      color: $button-color;
      &:hover {
        color: $white-color;
        background-color: $button-color;
      }
      &_active {
        color: $white-color;
        background-color: $button-color;
      }
    }
  }
  .portfolio-pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    &-wrapper {
      padding: $side-margin 0;
      @include slogan-gradient();
    }
    li {
      display: inline-block;
      height: 60px;
      width: 60px;
      margin-right: 2px;
      &:first-child {
        margin-right: 100px;
      }
      &:last-child {
        margin-left: 100px;
      }
    }
    button {
      display: inline-block;
      width: 100%;
      height: 60px;
      margin: 0;
    }
    .v-pagination__navigation {
      color: $white-color !important;
      box-shadow: none;
      font-size: rem(32);
      background-color: transparent !important;
      i {
        color: $white-color !important;
      }
    }
    .v-pagination__item {
      font-size: rem(32);
      color: $white-color !important;
      font-weight: 300;
      background-color: transparent !important;
      box-shadow: none;
      border-radius: 0;
      min-width: 60px !important;
      padding: 0 !important;
      &:hover {
        border: 1px solid $white-color;
      }
      &.primary {
        width: 100%;
        padding: 0 !important;
        background-color: $navigation-link-active !important;
        border-color: $navigation-link-active !important;
      }
    }
  }
</style>



