<template>
  <div class="single-case">
    <div class="single-case--header">
      <div class="background" :style='{ backgroundImage: "url(" + caseItem.headerImage + ")", }'>
    </div>
      <v-layout row wrap
                align-center
                justify-centerfill-height>
          <v-flex xl12 class="text">
            <h2><b>{{caseItem.customer}}</b><span>/</span>{{caseItem.country}}</h2>
            <h1>{{caseItem.header}}</h1>
            <h2>{{caseItem.description}}</h2>
          </v-flex>
        </v-layout>
    </div>
    <div class="single-case--description">
    <v-layout row wrap class="single-case--description__line">
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>FIELD:</h3></div>
            <p class="content">{{caseItem.data.field}}</p>
          </div>
        </v-flex>
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>PLATFORMS:</h3></div>
            <p class="content">{{caseItem.data.platforms}}</p>
          </div>
        </v-flex>
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>ROLE:</h3></div>
            <p class="content">{{caseItem.data.role}}</p>
          </div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="single-case--description__line">
        <v-flex xl6>
          <div class="content-block-big">
            <div class="title"><h3>CLIENT:</h3></div>
            <p class="content">{{caseItem.data.client}}</p>
          </div>
        </v-flex>
        <v-flex xl6>
          <div class="content-block-big">
            <div class="title"><h3>RESPONSIBILITIES:</h3></div>
            <p class="content">{{caseItem.data.responsibilities}}</p>
          </div>
        </v-flex>
        <v-flex xl6>
          <div class="content-block-big">
            <div class="title"><h3>PECULIARITIES:</h3></div>
            <p class="content">{{caseItem.data.peculiarities}}</p>
          </div>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="single-case--description__line">
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>TYPE OF TEXTS:</h3></div>
            <p class="content">{{caseItem.data.type_of_text}}</p>
          </div>
        </v-flex>
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>LOCALIZATION TOOL:</h3></div>
            <p class="content">{{caseItem.data.localization_tool}}</p>
          </div>
        </v-flex>
        <v-flex xl4>
          <div class="content-block">
            <div class="title"><h3>DURATION:</h3></div>
            <p class="content">{{caseItem.data.duration}}</p>
          </div>
        </v-flex>
      </v-layout>
      <v-layout
        row wrap
        align-center
        justify-center
        class="single-case--description__line">
        <v-flex
          md6 xl6
          class="case-button">
          <a class="case-button--item" href="">Back to {{caseItem.category}}</a>
        </v-flex>
      </v-layout>
    </div>
    <div class="single-case--more">
      <SloganLine :slogan="slogan"/>
      <v-layout
        row wrap
        align-center
        justify-center
        class="portfolio-case-wrapper">
        <SingleCaseCell :someCase="caseItem"></SingleCaseCell>
        <SingleCaseCell :someCase="caseItem"></SingleCaseCell>
      </v-layout>
    </div>
  </div>
</template>

<script>
import SloganLine from '../components/shared/SloganLine';
import SingleCaseCell from '../components/shared/SingleCaseCell';

export default {
  name: 'PortfolioCase',
  props: ['category'],
  components: {
    SloganLine,
    SingleCaseCell,
  },
  data() {
    return {
      slogan: 'SEE MORE CASE STUDIES',
      page: 1,
      caseItem: {
        case_id: 0,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
        headerImage: '/static/case_bg.jpg',
        category: 'Marketing',
        data: {
          field: 'IT',
          platforms: 'Web, Windows, Mac OS, iOS, Android',
          role: 'Head localization specialist',
          client: 'A company providing file hosting service, headquartered in US, that offers cloud storage, file synchronization, personal cloud, and client software. This service allows users to create a special folder on their computers, which it then synchronizes so that it appears to be the same folder (with the same contents) regardless of which device is used to view it. Files placed in this folder are also accessible via the service’s website and mobile apps.',
          responsibilities: 'Translate content, edit and review the translated content\n' +
            'Test HR, manage the localization team, provide team members with work instructions, assess quality of translations and provide feedbacks\n' +
            'Arrange and edit the terminology glossary\n' +
            'Compile and edit a Ukrainian language guide\n' +
            'Re-work and review already existing content translations provided by the previous localization team\n' +
            'Re-work and review already existing terminology translations compiled by the previous localization team\n' +
            'Handle client’s queries on the peculiarities of content\n' +
            'Provide linguistic QA-tests on different platforms\n' +
            'Communicate with client’s project managers and IT staff\n' +
            'Participate in meetings and conferences',
          peculiarities: 'Previously, the client had already worked with the Ukrainian localization team. However, the provided translations did not comply with the client’s requirements. The task was to both re-work the existing translations, and provide localization of new content subdivided into several projects: Web file hosting service, desktop client, Android app, iOS app, video and audio promotion content, e-mail communication, legal content, Android app for photos, iOS app for photos, and Web service for photos.',
          type_of_text: 'UI, technical, marketing, legal',
          localization_tool: 'Smartling cloud-based tool',
          duration: 'On daily basis since February, 2015 \n' +
            'up to present.',
        },
      },
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
.single-case {
  &--header {
    height: 100vh;
    display: flex;
    position: relative;
    color: $white-color;
    .text {
      z-index: 2;
      h2 {
        font-weight: 400;
      }
      span {
        margin: 0 rem(21);
      }
    }
    .background {
      z-index: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background-size: cover;
      background-position: center;
      transform-origin: 50% 50% 0;
      transform: translate3d(0,0,0px);
      transition: -webkit-filter 0.15s,transform 0.5s;
      transition-timing-function: ease-out;
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        z-index: 2;
        background-color: rgba(0, 0, 0, 0.51);
      }
    }
  }
  &--description {
    text-align: left;
    margin-bottom: $slogan-margin;
    padding: 0 $padding-case-content;
    &__line {
      margin-top: $slogan-margin;
    }
    h3 {
      color: $navigation-link-active;
    }
    .content-block {
      padding-right: rem(60);
      &-big {
        padding-right: rem(120);
      }
    }
    .title {
      margin-bottom: $basic-padding;
    }
  }
  .case-button {
    text-align: center;
    &-wrapper {
      margin-top: $slogan-margin;
      margin-bottom: $button-margin;
    }
    &--item {
      text-align: center;
      display: inline-block;
      border: 1px solid $button-color;
      max-width: 428px;
      width: 100%;
      padding: $basic-padding 0;
      margin-bottom: $button-margin;
      color: $button-color;
      &:hover {
        color: $white-color;
        background-color: $button-color;
      }
      &_active {
        color: $white-color;
        background-color: $button-color;
      }
    }
  }
}
</style>



