<template>
  <div class="services">
    <SloganLine :slogan="slogan"/>
    <div class="services--header">
      <v-playback class="services--header__player" :url="videoUrl"></v-playback>
      <span class="play-btn"></span>
      <v-layout row wrap
                align-center
                justify-centerfill-height>
        <v-flex xl12 class="text">
        </v-flex>
      </v-layout>
    </div>
    <div class="services--description">
      <v-layout
        justify-center
        row fill-height
        wrap
        class="single-case--description__line">
        <v-flex md6 xl6 class="services--description__content">
          <div v-for="(service, i) in services_left"
               :key="i"
               class="services--description__content-block">
            <div class="services--description__title"><h3>{{service.title}}</h3></div>
            <p class="services--description__content">{{service.content}}</p>
            <router-link
              class="services--description__button"
              :to="{name: 'ServicesPortfolio', params: {service: service.title}}"
            >Case studies</router-link>
          </div>
        </v-flex>
        <v-flex md6 xl6 class="services--description__content">
          <div v-for="(service, i) in services_right"
               :key="i"
               class="services--description__content-block">
            <div class="services--description__title"><h3>{{service.title}}</h3></div>
            <p class="services--description__content">{{service.content}}</p>
            <router-link
              class="services--description__button"
              :to="{name: 'ServicesPortfolio', params: {service: service.title}}"
            >Case studies</router-link>
          </div>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import vPlayBack from 'v-playback';
import SloganLine from '../components/shared/SloganLine';

export default {
  name: 'Services',
  components: {
    vPlayBack,
    SloganLine,
  },
  mounted() {
  },
  data() {
    return {
      slogan: 'LINGUISTICALLY TAILORED.',
      videoUrl: 'static/video.mp4',
      services_left: [{
        title: 'TRANSLATION',
        content: 'From marketing to finance, from life sciences to technical \n' +
          'manuals and everything else in between. We provide high \n' +
          'quality translations and excellent customer service.',
      }, {
        title: 'PROOFREADING & EDITING',
        content: 'This step presumes linguistic reviewing of content, \n' +
          'correcting mistakes, improving text quality and readability.',
      }, {
        title: 'TRANSCREATION',
        content: 'Make sure cultural references in your ad or slogan are \n' +
          'coming  across the way you intended. Branding, Copy \n' +
          'Origination.',
      }, {
        title: 'SEO ADAPTATION',
        content: 'We’ll write multilingual engaging and SEO-friendly  \n' +
          'content that  will help the right people find you.',
      }],
      services_right: [{
        title: 'LOCALIZATION',
        content: 'In today’s world, localizing your content or product  is key \n' +
          'to international success. Localize your website or software \n' +
          'today and reach new markets.',
      }, {
        title: 'QUALITY ASSURANCE',
        content: 'Language quality assurance implies testing of content \n' +
          'from linguistic point of view. It may presume auditing \n' +
          '(final text control), reporting and quality assessment.',
      }, {
        title: 'MARKET RESEARCH',
        content: 'In today’s world, localizing your content or product  is key \n' +
          'to international success. Localize your website  or software \n' +
          'today and reach new markets.',
      }, {
        title: 'PROJECT MANAGEMENT',
        content: 'Language quality assurance implies testing of  content \n' +
          'from linguistic point of view. It may presume auditing \n' +
          '(final text control), reporting and quality assessment.',
      }],
    };
  },
  methods: {
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .services {
    &--header {
      height: 100vh;
      display: flex;
      position: relative;
      color: $white-color;
      &__player {
        position: relative;
        width: 100%;
        max-height: 100vh;
        overflow: hidden;
        border-top: 2px solid $white-color;
        .video-js {
          max-height: 100vh;
          position: relative;
          .vjs-big-play-button {
            background-color: $white-color;
            border-radius: 50%;
            height: rem(100);
            width: rem(100);
            &:hover {
              background-color: $navigation-link-active;
              span {
                color: $white-color;
              }
            }
            span {
              color: $navigation-link-active;
              font-size: rem(42);
            }
            .vjs-icon-placeholder {
              &:before {
                position: absolute;
                top: 18px !important;
              }
            }
          }
        }
      }
      .background {
        z-index: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: cover;
        background-position: center;
        transform-origin: 50% 50% 0;
        transform: translate3d(0, 0, 0px);
        transition: -webkit-filter 0.15s, transform 0.5s;
        transition-timing-function: ease-out;

        &:before {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          z-index: 2;
          background-color: rgba(0, 0, 0, 0.51);
        }
      }
    }
    &--description {
      position: relative;
      text-align: left;
      padding: $slogan-margin $padding-case-content;
      p {
        margin-bottom: 0;
      }
      &__content {
        padding-bottom: $basic-padding;
      }
      &__content-block {
        padding-bottom: $slogan-margin;
        max-width: rem(400);
        margin: auto;
        &:last-child {
          padding-bottom: 0;
        }
      }
      &__title {
        color: $navigation-link-active;
        padding-bottom: $basic-padding;
      }
      &__button {
        display: inline-block;
        color: $navigation-link-active;
        border: 1px solid $navigation-link-active;
        padding: $basic-padding $side-margin;
        &:hover {
          color: $white-color;
          background-color: $navigation-link-active;
        }
      }
    }
    .services-button {
      text-align: center;
      &-wrapper {
        margin-top: $slogan-margin;
        margin-bottom: $button-margin;
      }
      &--item {
        text-align: center;
        display: inline-block;
        border: 1px solid $button-color;
        max-width: 428px;
        width: 100%;
        padding: $basic-padding 0;
        margin-bottom: $button-margin;
        color: $button-color;
        &:hover {
          color: $white-color;
          background-color: $button-color;
        }
        &_active {
          color: $white-color;
          background-color: $button-color;
        }
      }
    }
  }
</style>



