<template>
  <v-flex
    md6 xl6
    :key="i">
    <div class="portfolio-case">
      <router-link
        class="link"
        :to="{name: 'PortfolioCase', params: {case_id: someCase.case_id}}">
      </router-link>
      <div class="description">
        <div class="customer-details">
          <p><b>{{someCase.customer}}</b><span>/</span>{{someCase.country}}</p>
        </div>
        <h2>{{someCase.header}}</h2>
        <h3 class="hover-info">{{someCase.description}}</h3>
      </div>
      <img :src="someCase.src" alt="">
    </div>
  </v-flex>
</template>

<script>
export default {
  name: 'SingleCaseCell',
  props: ['someCase'],
  data() {
    return {
      slogan: 'LANGUAGE SPECIFIC.',
      page: 1,
      categories: ['Marketing', 'Business & Financial', 'It & Games',
        'Technical', 'Hi-Tec', 'Fictional', 'Legal', 'Articles'],
      cases: [{
        case_id: 0,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 1,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 2,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }, {
        case_id: 4,
        header: 'FILE HOSTING SERVICE',
        customer: 'IT CORPORATION',
        description: 'English to Ukrainian localization',
        country: 'USA',
        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
      }],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .portfolio-case {
    display: block;
    color: $white-color;
    margin-bottom: 0px;
    border: 1px solid $white-color;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: -webkit-filter 0.45s,transform 0.5s;
    transform-origin: 50% 50% 0;
    transform: translate3d(0,0,0px);
    transition-timing-function: ease-out;
    &:hover {
      img {
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
      }
      .description {
        .hover-info {
          display: inline;
        }
      }
      &:before {
        transition: background-color 0.35s cubic-bezier(0.645,0.045,0.355,1);
        background-color: rgba(0, 0, 0, 0.71);
      }
    }
    &:before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      z-index: 1;
      background-color: rgba(0, 0, 0, 0.51);
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
      z-index: 0;
    }
    .description {
      z-index: 2;
      text-align: left;
      width: 80%;
      position: absolute;
      span {
        padding: 0 $basic-padding;
      }
      p {
        @include letter-spacing(140);
        font-size: rem(17);
        b {
          font-weight: bold;
        }
        margin-bottom: 0;
      }
      .hover-info {
        display: none;
      }
      h3 {
        margin-top: $basic-padding;
        color: #888888;
      }
    }
    .link {
      display: inline-block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      z-index: 3;
    }
  }
</style>



