<template>
  <div class="feedbacks">
    <SloganLine :slogan="slogan"/>
    <div class="feedbacks--comments">
      <v-layout row wrap>
        <v-flex
          xl12 md12
          v-for="(comment, i) in comments"
          :key="i"
          class="feedbacks--comments__item">
          <div class="comment-block">
            <div class="comment-block__logo">
              <div class="comment-block__logo-container">
                <img class="avatar" :src="comment.avatarUrl" alt="">
                <span class="client">{{comment.client_name}}</span>
              </div>
            </div>
              <div class="comment-block__text">
                <div class="comment">
                  <h3>{{comment.text}}</h3>
                </div>
              </div>
          </div>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import SloganLine from '../components/shared/SloganLine';

export default {
  name: 'About',
  components: {
    SloganLine,
  },
  data() {
    return {
      slogan: 'MEETING CLIENTS’ NEEDS',
      comments: [{
        client_name: 'ARCHLINUX',
        text: 'Good response, and he proceeded to clarify the contents of the translation before starting work, for the quality. He is a trustworthy translator.',
        avatarUrl: '/static/client_logo.png',
      }, {
        client_name: 'ARCHLINUX',
        text: 'Nick Golensky is very a professional and very reliable translator. I am very pleased with his service. Each project has been delivered by the deadline. He always communicates and keeps me informed about the progress on the work. This is a real pleasure to collaborate with such a professional and nice translator. I look forward working again with Nick.',
        avatarUrl: '/static/client_logo.png',
      }, {
        client_name: 'ARCHLINUX',
        text: 'Good response, and he proceeded to clarify the contents of the translation before starting work, for the quality. He is a trustworthy translator.',
        avatarUrl: '/static/client_logo.png',
      }, {
        client_name: 'ARCHLINUX',
        text: 'Nick Golensky is very a professional and very reliable translator. I am very pleased with his service. Each project has been delivered by the deadline. He always communicates and keeps me informed about the progress on the work. This is a real pleasure to collaborate with such a professional and nice translator. I look forward working again with Nick.',
        avatarUrl: '/static/client_logo.png',
      }],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .feedbacks {
    &--comments {
      &__item {
        height: 100%;
        .comment-block {
          border: 1px solid $white-color;
          min-height: 100%;
          display: flex;
          align-items: stretch;
          &__logo {
            width: 20%;
            display: inline-block;
            margin: auto 0;
            text-align: right;
            padding-right: rem(60);
            &-container {
              display: inline-block;
              width: auto;
              max-width: rem(120);
              text-align: center;
            }
            img {
              display: inline-block;
              height: 100%;
              width: 100%;
              max-width: 126px;
              max-height: 60px;
            }
            span {
              display: block;
              font-size: rem(17);
              font-weight: 600;
              margin-top: $basic-padding;
            }
          }
          &__text {
            background-color: $comment-background;
            text-align: left;
            padding: $slogan-margin rem(60) $slogan-margin;
            width: 80%;
            display: inline-block;
            .comment {
              h3 {
                display: inline-block;
              }
            }
          }
        }
      }
    }
  }
</style>



