<template>
  <div class="homepage">
    <SloganLine :slogan="slogan"/>
    <v-carousel hide-controls class="homepage-carousel" height="calc(90vh - 130px)">
      <v-carousel-item
        v-for="(item,i) in items"
        :key="i"
        :src="item.src"
      >
        <div class="homepage-carousel--content">
          <router-link
            class="link"
            :to="{name: 'PortfolioCase', params: {case_id: item.case_id}}">
          </router-link>
          <div class="description">
            <h2><b>{{item.customer}}</b></h2>
            <span>/</span>
            <h2>{{item.country}}</h2>
          </div>
          <div class="header"><h1>{{item.header}}</h1></div>
        </div>
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

<script>
import SloganLine from '../components/shared/SloganLine';

export default {
  name: 'Homepage',
  components: {
    SloganLine,
  },
  data() {
    return {
      slogan: 'SPEAK LOCAL. GO GLOBAL.',
      items: [
        {
          case_id: 0,
          header: 'FILE HOSTING SERVICE',
          customer: 'IT CORPORATION',
          country: 'USA',
          src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
        },
        {
          case_id: 1,
          header: 'FILE HOSTING SERVICE',
          customer: 'IT CORPORATION',
          country: 'USA',
          src: 'https://cdn.vuetifyjs.com/images/carousel/sky.jpg',
        },
      ],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/style/style.scss";
  .homepage {
    &-carousel {
      border-top: 2px solid $body-background-color;
      max-height: calc(90vh - 130px);
      h2 {
        display: inline-block;
        color: $white-color;
      }
      h1 {
        color: $white-color;
      }
      .v-icon {
        font-size: rem(16);
        opacity: 1;
      }
      &--content {
        position: relative;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        &:after {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          background-color: rgba(0, 0, 0, 0.51);
        }
        .link {
          position: absolute;
          height: 100%;
          width: 100%;
          z-index: 2;
        }
        .description {
          z-index: 1;
          text-align: left;
          width: 80%;
          b {
            font-weight: 700;
          }
          span {
            font-size: rem(32);
            color: $white-color;
            font-weight: 400;
            padding: 0 $basic-padding;
          }
        }
        .header {
          z-index: 1;
          text-align: left;
          width: 80%;
        }
      }
    }
  }
  .v-carousel.homepage-carousel {
    height: calc(90vh - 130px);
  }
  .v-carousel__controls {
    background: none;
  }
  .v-btn.v-btn--active.v-btn--icon {
    color: $slider-dots !important;
  }
  .v-btn.v-btn--icon {
    color: $body-background-color !important;
  }
</style>



